@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#votes {
  margin-left: 10px;
}

.container {
    padding: 20px;
    font-size: 1.3em;
}

.voteType {
    padding: 5px;
}

body { 
    background-color: white;
    font-family: 'Questrial', sans-serif;
}

.strikethrough {
    background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' version='1.1' preserveAspectRatio='none' viewBox='0 0 100 100'><path d='M100 0 L0 100 ' stroke='red' stroke-width='20'/><path d='M0 0 L100 100 ' stroke='red' stroke-width='20'/></svg>");
  background-repeat:no-repeat;
  background-position:center center;
  background-size: 100% 100%, auto;
  font-size: 1.2em;
  padding-top: 2px;
}

